import {IDocUser} from "../constants/interfaces";
import * as ExcelJS from "exceljs";
import {Style, Worksheet} from "exceljs";
import {saveAs} from "file-saver";
import {UserTypes} from "../enums";

class Workbook {
    private static instance: Workbook;

    private constructor() {
    }

    static getInstance(): Workbook {
        if (!Workbook.instance) {
            Workbook.instance = new Workbook();
        }

        return Workbook.instance;
    }

    async createAndSave(data: IDocUser[]) {
        // Create Workbook
        let workbook = new ExcelJS.Workbook();
        workbook.creator = 'UM iCamp 2020 Web System';
        workbook.created = new Date();
        workbook.modified = new Date();

        // Setup style for Header
        const headerStyle: Partial<Style> = {
            font: {
                bold: true,
            },
            alignment: {
                horizontal: "center",
            },
            border: {
                bottom: {
                    style: "double"
                }
            }
        }

        // Create 'General Info' worksheet
        let wsGeneralInfo: Worksheet = workbook.addWorksheet('General Info');
        wsGeneralInfo.columns = [
            {header: 'No', key: 'no', width: 5, alignment: {horizontal: "center"}},
            {header: 'Id', key: 'id', width: 35},
            {header: 'Email', key: 'email', width: 35},
            {header: 'Created at', key: 'createdAt', width: 15},
            {header: 'Submitted', key: 'isSubmitted', width: 12},
            {header: 'Personal Details', key: 'personalDetails', width: 20},
        ];
        wsGeneralInfo.getCell('A1').style = headerStyle;
        wsGeneralInfo.getCell('B1').style = headerStyle;
        wsGeneralInfo.getCell('C1').style = headerStyle;
        wsGeneralInfo.getCell('D1').style = headerStyle;
        wsGeneralInfo.getCell('E1').style = headerStyle;
        wsGeneralInfo.getCell('F1').style = headerStyle;

        // Create 'Personal Details' worksheet
        let wsPersonalDetails: Worksheet = workbook.addWorksheet('Personal Details');
        wsPersonalDetails.columns = [
            {header: 'No', key: 'no', width: 5, alignment: {horizontal: "center"}},
            {header: 'Id', key: 'id', width: 35},
        ];

        // Add Row to workbook
        data.filter(u => u.role === UserTypes.USER).forEach((user, index) => {
            // Add to General Info worksheet
            wsGeneralInfo.addRow([
                (index + 1),
                user.uid,
                user.email,
                user.createdAt?.toDateString(),
                user?.applicationForm?.configData?.isSubmitted,
            ]);
            wsGeneralInfo.getCell(`F${index + 2}`).value = {
                hyperlink: '#\'Personal Details\'!B' + (index + 2),
                text: `Personal Details`,
            };

            // Add to Personal Details worksheet
            wsPersonalDetails.addRow([
                (index + 1),
                user.uid,
            ]);
        });

        // Create buffer and meta data to save as a file
        const buffer = await workbook.xlsx.writeBuffer();
        const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet';
        const fileExtension = '.xlsx';
        const blob = new Blob([buffer], {type: fileType});
        saveAs(blob, `Daftar peserta UMiCamp 2020 - ${new Date().toDateString()}` + fileExtension);
    }
}

export default Workbook;